import request from '@/utils/request';
export default (offset, limit) => {
    return request({
        url: '/articles/get/home',
        method: 'POST',
        data: {
            offset,
            limit
        }
    });
};
export const getUser = (offset, limit, author) => {
    return request({
        url: '/articles/get/user',
        method: 'POST',
        data: {
            offset,
            limit,
            author
        }
    });
};
